interface HourInTresArroyosProps {
  hour: string;
}

const HourInTresArroyos: React.FC<HourInTresArroyosProps> = ({ hour }) => {

  const toTresArroyos = (hour: string): string => {
    return new Date(Date.parse(hour))
      .toLocaleString('es-AR', {
         hour: 'numeric', 
         minute: 'numeric', 
         second: 'numeric'
      });
  }

  return (
    <span>{toTresArroyos(hour)}</span>
  );
};

export default HourInTresArroyos;