import { IonContent, IonPage } from '@ionic/react';
import './Amanecer.css';

import { useState, useEffect } from 'react';
import { SunriseResponse } from '../models/SunriseResponse';
import HourInTresArroyos from "../components/HourInTresArroyos";
import ServiceStatus from "../components/ServiceStatus";
import TresArroyosHeader from "../components/TresArroyosHeader";

export interface AmanecerProps {
  latitude: number;
  longitude: number;
}

const Amanecer: React.FC<AmanecerProps> = ({ latitude, longitude }) => {

  const [error, setError] = useState<{ message: string } | null>(null);
  const [isLoaded, setIsLoaded] = useState(false);
  const [sunriseResponse, setSunriseResponse] = useState<SunriseResponse | null>(null);

  useEffect(() => {
    fetch(`https://api.sunrise-sunset.org/json?lat=${latitude}&lng=${longitude}&date=today&formatted=0`)
      .then(res => res.json())
      .then(
        (result: SunriseResponse) => {
          setIsLoaded(true);
          setSunriseResponse(result);
        },
        (error) => {
          setIsLoaded(true);
          setError(error);
        }
      )
  }, [latitude, longitude])

  if (error) {
    return (
      <div>Error: { error?.message }</div>
    );
  } else if (!isLoaded) {
    return (
      <div>Loading...</div>
    );
  } else if (sunriseResponse){
    return (
      <IonPage>
        <TresArroyosHeader />
        <IonContent fullscreen>
          <div className="container">

            <img src="https://sunrise-sunset.org/sunrise-100px.svg" alt="sunrise" width='100'/>
            <h1>Amanecer</h1>

            <h1>
              <HourInTresArroyos hour={sunriseResponse?.results?.sunrise} />
            </h1>
            
            <ServiceStatus status={sunriseResponse.status === 'OK'}/>
            
            

          </div>
        </IonContent>
      </IonPage>
    );
  }else{
    return (
      <div>Loading...</div>
      );
  }
};

export default Amanecer;
