import { Route } from 'react-router-dom';
import {
  IonApp,
  IonHeader,
  IonLabel,
  IonRouterOutlet,
  IonTabBar,
  IonTabButton,
  IonTabs,
  IonToolbar,
} from '@ionic/react';
import { IonReactRouter } from '@ionic/react-router';
import Amanecer from './pages/Amanecer';
import Ocaso from './pages/Ocaso';
import { Location } from './models/Location';

/* Core CSS required for Ionic components to work properly */
import '@ionic/react/css/core.css';

/* Basic CSS for apps built with Ionic */
import '@ionic/react/css/normalize.css';
import '@ionic/react/css/structure.css';
import '@ionic/react/css/typography.css';

/* Optional CSS utils that can be commented out */
import '@ionic/react/css/padding.css';
import '@ionic/react/css/float-elements.css';
import '@ionic/react/css/text-alignment.css';
import '@ionic/react/css/text-transformation.css';
import '@ionic/react/css/flex-utils.css';
import '@ionic/react/css/display.css';

/* Theme variables */
import './theme/variables.css';
import React, { useState } from "react";
import ChangeLocation from './components/ChangeLocation';
import { LOCATIONS } from './data/LocationSeed';



const App: React.FC = () => {
  const initialvalue = LOCATIONS[0];
  const [location, setLocation] = useState<Location>(initialvalue);
  
  const locationChanged = (l:Location)=>{
    setLocation(l);
  }
  return (
    <IonApp>
      <IonReactRouter>
        <IonHeader>
          <IonToolbar>
            <ChangeLocation onLocationChange={locationChanged} initialvalue={initialvalue} />
          </IonToolbar>
        </IonHeader>
        <IonTabs>
          <IonRouterOutlet>
            <Route exact path="/sunrise">
              <Amanecer latitude={location.latitude} longitude={location.longitude}/>
            </Route>
            <Route exact path="/sunset">
              <Ocaso  latitude={location.latitude} longitude={location.longitude} />
            </Route>
          </IonRouterOutlet>
          <IonTabBar slot="bottom">
            <IonTabButton tab="sunrise" href="/sunrise">
              <img src="https://sunrise-sunset.org/sunrise-100px.svg" alt="sunrise" width='30'/>
              <IonLabel>Amanecer</IonLabel>
            </IonTabButton>
            <IonTabButton tab="sunset" href="/sunset">
              <img src="https://sunrise-sunset.org/sunset-100px.svg" alt="sunrise" width='30'/>
              <IonLabel>Ocaso</IonLabel>
            </IonTabButton>
          </IonTabBar>
        </IonTabs>
  
      </IonReactRouter>
    </IonApp>
  );
}

export default App;
