
import { IonItem, IonLabel, IonSelect, IonSelectOption } from '@ionic/react';
import React, { useState } from 'react';
import { LOCATIONS } from '../data/LocationSeed';
import { Location } from "./../models/Location";


interface ChangeLocationProps {
  initialvalue: Location,
  onLocationChange: (l: Location) => void;
}


const ChangeLocation: React.FC<ChangeLocationProps> = ({ initialvalue, onLocationChange }) => {
  const [value, setValue] = useState<number>(initialvalue.id);
  const changed = (l: Location) => {
    console.log('selected', l);
    if (l){
      setValue(l.id);
      onLocationChange(l);
    }
  }
  
  return (
    <>
    <IonSelect interface="action-sheet" onIonChange={e => changed(LOCATIONS.find(x => x.id === e.detail.value)!)} value={value} >
      {
        LOCATIONS.map((x, index) => (
          <IonSelectOption key={index} value={x.id}>
            {x.name}
          </IonSelectOption>
        ))
      }
    </IonSelect>
  </>
  );
};

export default ChangeLocation;