interface ServiceStatusProps {
  status: boolean;
}

const ServiceStatus: React.FC<ServiceStatusProps> = ({ status }) => {

  const greenStyle = {
    color: 'green'
  };
  const redStyle = {
    color: 'red'
  };

  return (
    <small>servicio:
      {
        status
        ? <span style={greenStyle}>online</span>
        : <span style={redStyle}>offline</span>
      }
    </small>
  );
};

export default ServiceStatus;